












import Vue from 'vue';

export default Vue.extend({
  name: 'SelectChooseFormat',
  props: {
    format: {
      required: false,
      default: function () {
        return 'mp3'
      }
    },
  },
  methods: {
    updateFormat() {
      // @ts-ignore
      this.$emit('input', this.$refs.format.value)
    }
  }
});
